}

.attack-control {
  cursor: pointer;
  padding-bottom: 3px;
}

.svg {
  transform: translateY(50);
}

.add-attack {
  padding-top: 20px;
}

.attack {
  font-size: 16px;
}

.comparison {
  border: 3px solid black;
  border-radius: 5px;
  padding: 5px;
  margin-top: 25px;
  margin-bottom: 25px;
}


